@import "variables.scss";

.learnPage {
  display: flex;
  width: 100%;
  justify-content: center;
  background-color: $learnBackground;
  overflow-x: hidden;
}
.orangeTitle {
  color: $secondaryColor;
}
.LeftLearnSection {
  padding-right: 62px;
}
.RightLearnSection {
  border-left: 1px solid rgb(0, 0, 0, 0.1);
  padding-left: 62px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 56px;
}
.learnImage {
  padding: 0;
  margin: 0;
}
.learnTitile {
  margin-top: 16px;
  margin-bottom: 16px;
}
.agencyTitile {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  line-height: 110%;

  letter-spacing: -0.03em;
}
.patagraphLearn {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  color: $grayParagraph;
}
.learnComponent {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 40px;
  padding: 100px;
  max-width: 1160px;
  @media (max-width: $breakpoint) {
    margin: 2vw;
    padding: 5vw;
  }
  @media (max-width: $breakpoint2) {
    flex-direction: column;
    align-items: center;
    height: auto;
    margin: 0;
    margin-top: 0;
    padding: 2vw;
    padding-top: 6vw;
  }
}

@media (max-width: $breakpoint2) {
  .LeftLearnSection {
    padding: 0;
    width: 100%;
    margin-bottom: 10px;
  }
  .learnComponent {
    padding: 20px;
  }
  .RightLearnSection {
    padding: 0;
    width: 100%;
    gap: 10px;
    border: none;
  }
  .patagraphLearn {
    margin: 0;
  }
}
