@import "variables";
a {
  color: inherit;
  text-decoration: none;
}
.containerNav {
  
  overflow-x: hidden;  
  z-index: 400;
  background-color: #ffffff;
  top: 0px;
  position: sticky;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
}
.headerContainer {
  overflow: hidden;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: $containerWidth;
}
nav ul li {
  list-style-type: none;
  padding: 0;
  display: inline;
  text-decoration: none;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  a {
    color: $primaryLight;
    text-decoration: none;
  }
  a:hover {
    color: #000000;
    transition: color 0.3 ease;
  }
}
.menu {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 40px;
  @media (max-width: $breakpoint) {
    gap: 1vw;
  }
  @media (max-width: $breakpoint2) {
    display: none;
    visibility: hidden;
  }
}
.logo {
  display: flex;
  align-items: center;
}
.mobileMenu {
  display: none;
  visibility: hidden;
  font-size: 40px;
  height: 40px;
  ul li {
    margin: 30px;
  }
}
.mobileSectionsOpen {
  position: absolute;
  height: 100vh;
  width: 70vw;
  top: 0;
  right: 0;

  display: none;
  visibility: hidden;

  z-index: 200;
  transition: all 0.2s ease;
  background-color: $primary;
  color: $backgroundColor;
}
.CloseButton {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 40px;
  height: 40px;
}
.signUpMobile {
  background: #fff0ec;
  border-radius: 50px;
  color: $secondaryColorDark;
  padding: 8px 18px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
}
.mobileList {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 20px;
  font-size: 25px;
  nav ul li {
    list-style-type: none;
    padding: 0;
    font-size: 20px;
    text-decoration: none;
  }
  a {
    color: $primaryLight;
    text-decoration: none;
  }
  a:hover {
    color: #000000;
  }
}
.select {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
  select {
    font-weight: 700;

    width: 45px;
    border: none;
    border-radius: none;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding-right: 10px;
    padding: 0;
    background-position: right;
    background-image: url(../images/select-arrow.png);
    background-repeat: no-repeat;
    gap: 8;
    background-size: 10px;
  }
}
.mobileSectionsClose {
  position: absolute;
  height: 100vh;
  width: 70vw;
  top: 0;

  display: none;
  visibility: hidden;
  right: -70vw;
  z-index: 200;
  transition: all 0.2s ease;
  background-color: $primary;
  color: $backgroundColor;
}
.mobileMenu {
  display: none;
  visibility: hidden;
}
.controlContainer {
  display: flex;
  flex-direction: row;
}
@media (max-width: 768px) {
  .containerNav {
    top: 0;
  }
  .logo {
    padding: 10px;
  }
  .mobileMenu {
    padding: 10px;
    display: block;
    visibility: visible;
    font-size: 40px;
    color: black;
    height: 40px;
  }
  .mobileSectionsOpen {
    position: fixed;
    height: 100vh;
    width: 70vw;
    top: 0;
    transform: translateX(70vw);

    display: block;
    visibility: visible;
    z-index: 200;
    transition: all 0.2s ease;
    background-color: $primary;
    color: $backgroundColor;
  }
  .mobileSectionsClose {
    position: fixed;
    height: 100vh;
    width: 70vw;
    top: 0;

    display: block;
    visibility: visible;

    transform: translateX(-70vw);
    z-index: 200;
    transition: all 0.2s ease;
    background-color: $primary;
    color: $backgroundColor;
  }
  .headerContainer {
    width: 100%;
    justify-content: space-between;
  }
  .sections {
    display: none;
    visibility: hidden;
  }
}
