@import "./variables.scss";
.footerPage {
  position: absolute;
  background: #3b434f;
  margin: 0;
  bottom: 0;
  width: 100%;
  margin-bottom: -50%;
  padding-top: 5%;
  z-index: -5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
}

.logoIMG {
  margin: 0.5vw;
}
.footerParagraph {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 1vw;
  text-align: center;
  color: #ffffff;
  opacity: 0.5;
}

.rightColumn {
  padding: 10px;
}
.footerText {
  font-size: 1vw;
  
font-family: 'Poppins';
font-style: normal;
font-weight: 400;
color: #FFFFFF;

opacity: 0.5;
}
.row2 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.FooterContainer {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
}
@media (max-width: 768px) {
  
.footerParagraph {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 3vw;
  text-align: center;
  color: #ffffff;
  opacity: 0.5;
}

  .footerPage {
    padding-top: 40vw;
    margin-bottom: -100%;
  }
  .footerText {
    font-size: 3vw;
  }
  .logoIMG {
    margin: 0.5vw;
    height: 5vw;
    width: auto;
  }
}
