@import "variables";
.MainContainer {
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  padding: 0;

  overflow-x: hidden;
}

.heroContainer {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: $containerWidth;
}
.patagraphHeroH {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  color: #677487;
}

.agencyTitile {
  color: $primary;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 110%;
  letter-spacing: -0.03em;
  margin: 0;
}
.boxTitle {
  color: $secondaryColor;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 110%;
  letter-spacing: -0.03em;
  margin: 0;
}

.SignUpHero {
  margin-top: 16px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.EnterYourEmail {
  box-sizing: border-box;
  width: 411.69px;
  height: 60px;
  left: 140px;
  top: 472px;
  padding: 21px 14px;
  font-size: 18px;
  background: #ffffff;
  border: 1px solid #dfe8fa;
  border-radius: 15px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;

  line-height: 180%;
}

.signUpParagraph {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 146%;
  margin-left: 30px;
  color: #bbc0c7;
}
.companyIMG {
  width: auto;
  height: 2vw;
}
.rating {
  margin: 0;
  padding: 0;
}

.companiesSVGs {
  display: flex;
  align-items: center;
  justify-content: start;
}
@media (max-width: $breakpoint) {
  .MainContainer {
    display: flex;
  }
  .heroContainer {
    width: 100%;
    padding: 2vw;
    padding-top: 0;
    justify-content: center;
  }
  .leftSection {
    width: 35vw;
    margin: 0;
  }
  .agencyTitile {
    font-size: 5vw;
  }
  .boxTitle {
    font-size: 5vw;
  }
  .patagraphHeroH {
    font-size: 4vw;
  }
  .rightSection {
    width: 55vw;
    height: auto;
  }
  .rightImage {
    width: 55vw;
    height: auto;
    margin: 0;
  }
  .EnterYourEmail {
    box-sizing: border-box;
    width: 35vw;
    height: 5vw;
    font-size: 15px;
  }
  .companyIMG {
    width: 10vw;
    height: auto;
  }
  .ratingIMG {
    width: auto;
    height: 2vw;
  }
  .signUpParagraph {
    font-size: 1.5vw;
  }
}

@media (max-width: $breakpoint2) {
  .MainContainer {
    margin-top: 20px;
    width: 100%;
    padding: 0;
  }
  .heroContainer {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;

    padding: 2vw;
    margin: 0;
    padding-top: 0;
  }
  .leftSection {
    padding: 10px;
    width: 100%;
  }
  .agencyTitile {
    font-size: 10vw;
  }
  .boxTitle {
    font-size: 10vw;
  }
  .patagraphHeroH {
    margin: 1vw;
    font-size: 3vw;
  }
  .rightSection {
    width: 100%;
    height: auto;
  }
  .rightImage {
    margin-left: 5%;
    width: 95%;
    height: auto;
  }
  .EnterYourEmail {
    box-sizing: border-box;
    width: 60vw;
    height: 5vw;
    font-size: 4vw;
  }
  .companyIMG {
    width: auto;
    margin: 1vw;
    height: 5vw;
  }
  .ratingIMG {
    width: auto;
    height: 5vw;
  }
  .signUpParagraph {
    margin: 2vw;
    font-size: 3vw;
  }
  .SignUpHero {
    margin: 0;
  }
}
