@import "./variables.scss";
.PracingPage {
  display: flex;
  min-width: 100%;
  justify-content: center;
  overflow-x: hidden;
  background-color: #fffbfa;
}
.LeftPricingSection {
  padding-right: 62px;
}
.RightPricingSection {
  padding: 5vw 2vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 56px;
}
.learnImage {
  padding: 0;
  margin: 0;
}
.pricingComponent {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 40px;
  padding: 100px;
  max-width: 1160px;
  @media (max-width: $breakpoint) {
    margin: 2vw;
    padding: 5vw;
  }
  @media (max-width: $breakpoint2) {
    flex-direction: column-reverse;
    align-items: center;
    margin: 0;
    margin-top: 0;
    padding: 2vw;
    padding-top: 6vw;
  }
}
.pricingImage {
  max-width: 100%;
}
.pricing {
  top: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  padding: 0;
  margin: 0;
  height: 100%;
  h1 {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 120px;
    line-height: 80%;
    letter-spacing: -0.03em;
    margin: 0;
  }
  p {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 180%;
    margin: 16px;
    color: #1d2736;
  }
}
@media (max-width: $breakpoint) {
  .pricingImage {
    width: 100%;
    height: auto;
  }
}
.signUpCont {
  margin-top: 50px;
}
.features {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-flow: row wrap;
  margin-top: 40px;
  margin-left: -8px;
  p {
    border-radius: 24px;
    padding: 8px 20px;
    margin: 8px;
    font-size: 18px;
    flex: none;
    order: 0;
    flex-grow: 0;
  }
  .option1 {
    background: #fff1ef;
    border: 1px solid #ffd1ca;
    color: #ff8b7a;
  }
  .option2 {
    background: #fff8ee;
    border: 1px solid #ffca7a;
    color: #ffac2f;
  }
  .option3 {
    color: #3acd77;
    background: #ecfff4;
    border: 1px solid #98fac0;
  }
  .option4 {
    color: #2bc0ca;
    background: #f1feff;
    border: 1px solid #6ee1e9;
  }
  .option5 {
    color: #c140fd;
    background: #fcf5ff;
    border: 1px solid #ebbdff;
  }
}

@media (max-width: $breakpoint2) {
  .LeftPricingSection {
    padding: 0;
    width: 100%;
    margin-bottom: 10px;
  }
  .signUpCont {
    margin: 0;
    margin-top: 10px;
  }
  .pricingImage {
    width: auto;
  }
  .pricing {
    height: 110vw;
    h1 {
      font-size: 17vw;
    }
    p {
      font-size: 4vw;
    }
  }
  .pricingComponent {
    padding: 20px;
  }
  .RightPricingSection {
    margin: 0;
    padding: 0;
    width: 100%;
    border: none;
  }

  .features {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    flex-flow: row wrap;
    margin-top: 20px;
    margin-left: -8px;
    p {
      border-radius: 24px;
      padding: 8px 20px;
      margin: 8px;
      font-size: 3vw;
      flex: none;
      order: 0;
      flex-grow: 0;
    }
  }
}
