@import "./variables";
.contactsalesPage {
  min-height: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fffafe;
  padding: 100px;
  z-index: 0;
}
.leftTestimonialS {
  margin-right: 20px;
}
.form {
  margin-bottom: -50%;
  z-index: 5;
  background: #ffffff;
  box-shadow: 0px -10px 150px rgba(80, 88, 100, 0.05);
  border-radius: 20px;
  padding: 120px 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.forms {
  display: flex;
  flex-direction: row;
  margin-bottom: 35px;
  gap: 32px;
}
.formTitile {
  padding: 0;
  margin: 0;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #1d2736;
}
.leftForm {
  gap: 32px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.contactForm {
  background: #f7f9fd;
  border: 1px solid #dfe8fa;
  border-radius: 15px;
  padding: 24px 32px;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  color: #677487;
  width: 25vw;
}
.testimonialTitile {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 180%;
  color: #1d2736;
}
.testimonialParagraph {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  color: #1d2736;
}
.testimonial {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media (max-width: $breakpoint2) {
  .leftTestimonialS {
    margin: 0;
  }
  .imageT {
    max-width: 50vw;
  }
  .leftForm {
    width: 90vw;
    input {
      padding: 5vw;
      width: 80vw;
    }
  }
  .forms {
    flex-direction: column;
    align-items: flex-start;
  }
  .form {
    z-index: 5;
    padding: 10px;
    padding-bottom: 50px;
    margin-bottom: -50%;
  }
  .contactForm {
  }
  .contactsalesPage {
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fffafe;
  }
  .testimonial {
    padding: 20px;
    flex-direction: column;
  }
  .testimonials {
    max-width: 100%;
  }


  .testimonialParagraph {
    max-width: 100%;
    margin: 0;
    padding: 0;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 4vw;
    line-height: 180%;
    color: #1d2736;
  }
}
