@use "sass:math";

$containerWidth: 1160px;
$containerPadding: 80;
$containerSidePadding: math.div($containerPadding, 2);

$breakpoint: 1200px;
$breakpoint2: 768px;
//colors
$primary: #1d2736;
$primaryLight: #717a90;
$grayParagraph: #677487;
$backgroundColor: #ffffff;
$secondaryColor: #f0998d;
$secondaryColorDark: #fd7461;
$heroBackground: #fdf9f8;
$learnBackground: #f9f7fc;
//global
.nav_btns {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 31px;
  @media (max-width: 768px) {
    left: auto;
  }
}
a {
  cursor: pointer;
}

.rounded_btn {
  background: #fff0ec;
  border-radius: 50px;
  color: $secondaryColorDark;
  top: 12px;
  padding: 12px 24px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
  :hover {
    background-color: $primary;
    transition: background 0.3s ease;
  }
  @media (max-width: 768px) {
    display: none;
    visibility: hidden;
  }
}
.rounded_btn:hover {
  background-color: $primary;
  transition: background 0.3s ease;
}

.signUpButton {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  padding: 20px 45px;
  background: #f0998d;
  box-shadow: 0px 16px 40px rgba(255, 188, 166, 0.33);
  border-radius: 15px;
}
@media (max-width: $breakpoint) {
  .signUpButton {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 3vw;
    color: #ffffff;
    padding: 1vw 2vw;
    background: #f0998d;
    border-radius: 15px;
  }
}
@media (max-width: $breakpoint2) {
  .signUpButton {
    font-size: 4vw;
    padding: 1.5vw 3.2vw;
    background: #f0998d;
    border-radius: 15px;
  }
  .mobileLayout {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .learnTitile {
    margin: 16px;
    font-size: 4vw;
  }
}

.mainText {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 46px;
  line-height: 110%;
  letter-spacing: -0.03em;
}

.NavigationUseCases {
  top: 100px;
  position: absolute;
}

.patagraphHero {
  color: $grayParagraph;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  padding: 0;
  margin-top: 16px;
}
.placesParagraph {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 180%;
  letter-spacing: 0.2em;
  color: $grayParagraph;
}
