@import'components/variables';

html {
  background-color: $backgroundColor;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-family: "Poppins", sans-serif;
}


// colors
$primary: #1D2736;
$prinaryLight: #677487;
$secondary: #F0998D;

.App {
  color: $primary;
  display: flex;
  padding: 0;
  margin: 0;
  margin-top: 32px;
  flex-direction: column;
  align-items: center;
  @media (max-width: $breakpoint2) {
    margin: 0;

  }
}