@import "variables.scss";

.UseCasesPage {
  display: flex;
  min-width: 100%;
  justify-content: center;
  overflow-x: hidden;
  background-color: $heroBackground;
}
nav ul li {
  list-style-type: none;
  padding: 0;
  display: inline;
  text-decoration: none;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  a {
    color: $primaryLight;
    text-decoration: none;
  }
  a:hover {
    color: #000000;
    transition: color 0.3 ease;
  }
}
.UseCasesContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  height: 700px;
  margin: 40px;
  padding: 100px;
  max-width: 1160px;
}
.secondaryText {
  color: $secondaryColor;
}

.placesUl {
  list-style-type: none;
  padding: 0;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  margin-left: -15px;
  margin-bottom: 3vw;
  li {
    border-right: 1px solid rgb(0, 0, 0, 0.1);
    line-height: 16px;
    padding: 0 15px;
  }
  nav ul li {
    color: #1d2736;
  }
}

#lastItem {
  border-right: none;
}

.rightUseSection {
}

.rightUseImage {
  width: 100%;
  height: auto;
}

.useCasesNavbar {
  list-style-type: none;
  position: absolute;
  z-index: 50;
  margin: 100px;
  text-decoration: none;
  display: flex;
  gap: 48px;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 180%;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  color: #1d2736;
  li {
    padding: 0;
    cursor: pointer;
    border-bottom: 3px solid transparent;
  }
  nav ul li {
    color: #1d2736;
  }
  li:hover {
    border-bottom: 3px solid $secondaryColor;
  }
}

@media (max-width: 1000px) {
  .leftSection {
    padding-top: 3vw;
  }
  .useCasesNavbar {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 20px;
    padding: 0;
    gap: 2vw;
  }
  .patagraphHero {
    word-break: break-all;
    margin: 2vw;
    font-size: 2.5vw;
  }
  .UseCasesContainer {
    margin: 2vw;
    padding: 5vw;
  }
  .MainText {
    margin: 2vw;
    font-size: 4vw;
  }
  .placesParagraph {
    margin: 2vw;
    font-size: 2.5vw;
  }
  .placesUl {
    margin: 2vw;
    font-size: 2.5vw;
  }
}
@media (max-width: $breakpoint2) {
  .useCasesNavbar {
    font-size: 4.5vw;
    margin: 0;
    margin-top: 5px;
  }
  .leftSection {
    padding-top: 2vw;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .UseCasesContainer {
    flex-direction: column;
    align-items: center;
    height: auto;
    margin: 0;
    padding: 2vw;
    padding-top: 6vw;
    background-color: $heroBackground;
  }

  .MainText {
    margin: 2vw;
    margin-top: 6vw;
    font-size: 6vw;
  }
  .patagraphHero {
    margin: 2vw;
    font-size: 3.5vw;
  }
  .placesUl {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0vw;
    font-size: 3vw;
  }
  .placesParagraph {
    font-size: 4vw;
    margin: 0;
    margin-left: 2vw;
  }
}
